import { format2hourMinute, formatDate } from '@/utils/date';
import 'solid-js/web';
import Actions from './components/Actions';
import { Column } from './components/RecordTable';

const BaseColumns: Column[] = [
  {
    title: '',
    dataIndex: 'dateStr',
    render: (v) => {
      return ''.charAt(new Date(v).getDay());
    },
  },
  {
    title: '',
    dataIndex: 'isWorkday',
    render: (v) => {
      return v ? '' : '';
    },
  },
  {
    title: '()',
    dataIndex: 'workMillSeconds',
    render: (v) => {
      if (v === 0) {
        return '';
      }

      const { hour, minute } = format2hourMinute(v);
      return `${hour}${minute}`;
    },
  },
  {
    title: '',
    dataIndex: 'startStr',
    render: (v, row) => {
      const isAction = row.isPersonalLeave || row.isTagStartCheckIn || row.isAbsenteeism;

      return (
        <div
          style={{
            display: 'flex',
            'align-items': 'center',
            'justify-content': 'space-between',
          }}
        >
          {!row.isTagStartCheckIn ? (
            v?.substring(11)
          ) : (
            <span
              data-bs-toggle="tooltip"
              data-bs-placement="top"
              title=", "
              style={{ cursor: 'pointer' }}
            >
              <span
                style={{
                  color: 'red',
                }}
              >
                {v?.substring(11)}
              </span>
            </span>
          )}

          {isAction ? (
            <div>
              <Actions row={row} value={v} type="start" />
            </div>
          ) : null}
        </div>
      );
    },
  },
  {
    title: '',
    dataIndex: 'endStr',
    style: {
      width: '250px',
    },
    render: (v, row) => {
      const isAction = row.isPersonalLeave || row.isTagEndCheckIn || row.isAbsenteeism;

      return (
        <div
          style={{
            display: 'flex',
            'align-items': 'center',
            'justify-content': 'space-between',
          }}
        >
          {!row.isTagEndCheckIn ? (
            v?.substring(11)
          ) : (
            <span
              data-bs-toggle="tooltip"
              data-bs-placement="top"
              title=", "
              style={{ cursor: 'pointer' }}
            >
              <span
                style={{
                  color: 'red',
                }}
              >
                {v?.substring(11)}
              </span>
            </span>
          )}

          {isAction ? (
            <div>
              <Actions row={row} value={v} type="end" />
            </div>
          ) : null}
        </div>
      );
    },
  },
  {
    title: '',
    dataIndex: 'attendance',
    style: {
      width: '150px',
    },
    render: (v: Date[]) => {
      if (!v.length) {
        return '';
      }

      return (
        <details>
          <summary></summary>
          {v
            .map((time) => {
              return new Date(time);
            })
            .map((date) => {
              return formatDate('hh:mm:ss', date);
            })
            .map((v) => {
              return (
                <>
                  {v} <br />
                </>
              );
            })}
        </details>
      );
    },
  },
];

const AbsenteeismColumns = [{ title: '', dataIndex: 'dateStr' }, ...BaseColumns];
const OvertimeColumns = [{ title: '', dataIndex: 'dateStr' }, ...BaseColumns];
const AllColumns = [{ title: '', dataIndex: 'dateStr' }, ...BaseColumns];

export { BaseColumns, AbsenteeismColumns, OvertimeColumns, AllColumns };
