function checkUrl() {
  // 
  const href = location.href;
  if (!href.includes('oa.hengtonggroup.com.cn')) {
    window.open('https://oa.hengtonggroup.com.cn/wui/main.jsp');
    return false;
  } else if (href.includes('/Login.jsp')) {
    alert('');
    return false;
  }

  return true;
}

export { checkUrl };
